import React from 'react';
import './List.css';
import FadeInSection from '../../FadeInSection/FadeInSection';

function List(props) {

    const data = Array.from(props.items);

    return (
        <FadeInSection>
            <div className="ReadingList">
                <h2>{props.time}</h2>
                <div className="ListView">
                    {data.map((item ,key)=>(
                        // <p key={key}>{item.author}</p>
                        <div className="Item">
                            <img alt={item.title} src={item.cover}/>
                            <h3 className="bookTitle">{item.title}</h3>
                            <p className="bookAuthor" id="secondary">{item.author}</p>
                        </div>
                    ))}
                </div>
            </div>
        </FadeInSection>
    )

}

export default List;